<template>
<div style="background-color: #F8F8F8;height: calc(100vh - 47px)">
  <div class="headerBox">
    <div class="userInfo">
      <h1 class="userName">用户名</h1>
      <p class="info">江西省上饶市XX区XX县猪场饲养员</p>
      <p class="info"><i class="iconfont">&#xe620;</i>&nbsp;江西 上饶</p>
    </div>
    <img class="userHeader" src="../../assets/head.jpg" />
  </div>
  <div class="userOther">
    <mt-cell title="问题反馈" to="//github.com" is-link></mt-cell>
    <mt-cell title="我要开户" to="//github.com" is-link></mt-cell>
    <mt-cell title="在线培训" to="//github.com" is-link></mt-cell>
    <mt-cell title="我的信息" to="//github.com" is-link></mt-cell>
    <br>
    <mt-cell title="注销" to="//github.com" is-link></mt-cell>
  </div>
</div>
</template>

<script>
export default {
  name: 'personInfo'
}
</script>

<style scoped lang="scss">
  .headerBox{
    display: flex;
    flex-flow: nowrap row;
    padding:42px 20px 25px;
    background-color: #fff;
    border-bottom: 1px solid #EBEBEB;
    & .userName{
      margin: 10px 0;
      font-size: 18px;
      font-weight: normal;
    }
    & .info{
      margin: 0;
      font-size:12px;
      color: #8A8E96;
      font-weight: 500;
    }
    & .userInfo{
      flex: 1;
    }
    & .userHeader{
      width: 75px;
      height: 75px;
    }
  }
  .cell-list{
    margin: 12px 0;
    & .cell:not(:first-child){
      border-top: none;
    }
  }
  .userOther{
    margin-top: 20px;
  }
  .cell{
    padding: 12px 24px;
    background-color: #fff;
    border-bottom: 1px solid #EBEBEB;
    border-top: 1px solid #EBEBEB;
    position: relative;
    &:active{
      background-color: #E1E1E1;
    }
  }
  .mint-cell{
    border-bottom: 1px solid #EBEBEB;
  }
</style>



